import React, { Fragment, useEffect, useState } from 'react'
import axios from 'axios'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../../viewhelper'
import { Link, useHistory } from 'react-router-dom'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import * as XLSX from 'xlsx'

const CreateNotificationGroup = () => {
    const [pointRuleloading, setPointRuleloading] = useState(false)
    const [excelFileError, setExcelFileError] = useState(null)  
    const [excelData, setExcelData] = useState([])
    const [userInput, setUserInput] = useState({
        group_map: [],
        group_name: ''
    })
    const user = JSON.parse(localStorage.getItem('userData'))
    const handleChange = (e) => {
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }

    const onSubmit = (e) => {
        e.preventDefault()
        const { group_name } = userInput
        let { group_map } = userInput
        group_map = excelData.map(e => {
             const a = { msisdn: e.msisdn, email: e.email }
             return a
        })
        setPointRuleloading(true)
        useJwt.createNotificationGroup({ group_name, group_map }).then((response) => {
            setPointRuleloading(false)
            Success(response)
          }).catch((error) => {
            setPointRuleloading(false)
            Error(error)
            console.log(error)
          })
    }
        // handle File
        const fileType = ['application/vnd.openxmlformats-officedocument.spreadsheetml.sheet']
        const handleFile = (e) => {
            const selectedFile = e.target.files[0]
            if (selectedFile) {
                if (selectedFile && fileType.includes(selectedFile.type)) {
                    const reader = new FileReader()
                    reader.readAsArrayBuffer(selectedFile)
                    reader.onload = (e) => {
                        setExcelFileError(null)
                        const workbook = XLSX.read(e.target.result, {type:'buffer'})
                        const worksheetName = workbook.SheetNames[0]
                        const worksheet = workbook.Sheets[worksheetName]
                        const data = XLSX.utils.sheet_to_json(worksheet)
                        setExcelData(data)
                        console.log(data)
                    } 
                } else {
                    setExcelFileError('Please select only excel file types')
                }
            } else {
                console.log('plz select your file')
            }
        }
    return (
        <Fragment>
            {
            user.role === 'vendor' ?  <Button.Ripple className='mb-1' color='primary' tag={Link} to='/allNotificationGroupsVendor' >
            <div className='d-flex align-items-center'>
                    <ChevronLeft size={17} style={{marginRight:'5px'}}/>
                    <span >Back</span>
            </div>
            </Button.Ripple> :  <Button.Ripple className='mb-1' color='primary' tag={Link} to='/allNotificationGroups' >
                    <div className='d-flex align-items-center'>
                            <ChevronLeft size={17} style={{marginRight:'5px'}}/>
                            <span >Back</span>
                    </div>
                    </Button.Ripple>
                        }
            <Card>
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Create Notificaion Group</CardTitle>
                    <Link to="/Group Upload File Template1.xlsx" target="_blank" download><FileText /> Download Template for Uploading Group</Link>
                </CardHeader>
                <CardBody style={{ paddingTop: '15px' }}>
                    <Form className="row" style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="group_name">Group Name</Label>
                            <Input type="text"
                                name="group_name"
                                id='group_name'
                                value={userInput.group_name}
                                onChange={handleChange}
                                required
                                placeholder="group name"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="12" >
                        <FormGroup>
                            <Label for="name">Upload a file</Label>
                            <Input type="file"
                                id='file'
                                onChange = { handleFile }
                                required
                            />
                             {excelFileError && <div className='text-danger'
                                style = {{marginTop: '5px'}}>{excelFileError}</div>}
                        </FormGroup>
                    </Col>
                    <br></br>
                    <hr></hr>
                    <Col sm="12">
                        {excelData !== null && (
                        <div className='table-responsive'>
                            <table className='table'>
                            <tbody>
                                {excelData.map(e => <tr key={e.__rowNum__}><th>{e.msisdn}</th><th>{e.email}</th></tr>)}
                            </tbody>
                            </table>            
                        </div>
                        )}       
                    </Col>
                        <Col sm="12" className='text-center'>
                            {
                                pointRuleloading ? <Button.Ripple color='primary' className='mr-1' disabled style={{ marginTop: '25px' }}>
                                    <Spinner color='white' size='sm' />
                                    <span className='ml-50'>Loading...</span>
                                </Button.Ripple> : <Button.Ripple className='ml-2' color='primary' type="submit" style={{ marginTop: '25px' }}>
                                    <span >Submit</span>
                                </Button.Ripple>
                            }
                        </Col>
                    </Form>
                </CardBody>
            </Card>
        </Fragment>
    )
}

export default CreateNotificationGroup